import React, { useEffect, useState } from "react";
import {
  AppBar,
  Autocomplete,
  Button,
  Chip,
  Container,
  Grid,
  Grow,
  Paper,
  TextField,
} from "@mui/material";
import Form from "../Forms/Form";
import Posts from "../Posts/Posts";
import { useDispatch } from "react-redux";
import { searchPostByQuery } from "../../actions/post";
import Pagination from "../Pagination";
import { useLocation, useNavigate } from "react-router-dom";
import styled from "@emotion/styled";

const StyledAppbar = styled(AppBar)({
  borderRadius: 4,
  marginBottom: "1rem",
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-between",
  padding: "16px",
});
const ListItem = styled("li")(({ theme }) => ({
  margin: theme.spacing(0.5),
}));

//For search field
function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function Home() {
  const [currentId, setCurrentId] = useState(null);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const query = useQuery();
  const page = query.get("page") || 1;
  const searchQuery = query.get("searchQuery");
  const [search, setSearch] = useState("");
  const [tags, setTag] = useState([]);
  console.log(tags);
  console.log(tags.length);

  const searchPost = () => {
    if (search.trim() || tags) {
      dispatch(
        searchPostByQuery({ search: search.trim(), tags: tags.join(",") })
      );
      navigate(`/posts/search?searchQuery=${search}&tags=${tags.join(",")}`);
    } else {
      navigate("/");
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      if (search.trim() || tags) {
        dispatch(
          searchPostByQuery({ search: search.trim(), tags: tags.join(",") })
        );
        navigate(`/posts/search?searchQuery=${search}&tags=${tags.join(",")}`);
      } else {
        navigate("/");
      }
    }
    //search posts
  };
  const handleAdd = (e) => {
    if (e.key == "Enter") {
      let value = e.target.value;
      setTag([...tags, value.trim()]);
      e.target.value = "";
      console.log(tags);
    }
  };
  const handleDelete = (value) => () => {
    setTag(tags.filter((tag) => tag !== value));
  };

  return (
    <Grow in>
      <Container maxWidth="xl">
        <Grid
          container
          justify="space-around"
          alignItems="stretch"
          spacing={2}
          sx={{
            flexDirection: {
              sm: "row",
            },
          }}
        >
          <Grid item xs={12} sm={6} md={9}>
            <Posts setCurrentId={setCurrentId} currentId={currentId} />
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <StyledAppbar position="static" color="inherit">
              <TextField
                id="outlined-basic"
                name="search"
                label="Search Recipes"
                value={search}
                variant="outlined"
                margin="normal"
                fullWidth
                onKeyDown={handleKeyPress}
                onChange={(e) => setSearch(e.target.value)}
              />
              {tags.length > 0 && (
                <Paper
                  sx={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "flex-start",
                    flexWrap: "wrap",
                    listStyle: "none",
                    p: 0.5,
                    m: 0,
                  }}
                  component="ul"
                >
                  {tags.map((data, index) => (
                    <ListItem key={index}>
                      <Chip label={data} onDelete={handleDelete(data)} />
                    </ListItem>
                  ))}
                </Paper>
              )}

              <TextField onKeyDown={handleAdd} placeholder="Search By tags.." />
              <Button
                onClick={searchPost}
                variant="contained"
                color="primary"
                sx={{ marginTop: 1 }}
              >
                Search
              </Button>
            </StyledAppbar>
            <Form currentId={currentId} setCurrentId={setCurrentId} />
            {!searchQuery && !tags.length && (
              <Paper elevation={6} sx={{ mt: 2, p: 1 }}>
                <Pagination page={page} variant="outlined" color="primary" />
              </Paper>
            )}
          </Grid>
        </Grid>
      </Container>
    </Grow>
  );
}

export default Home;
